body
{
  margin: 0;
  overflow-x: hidden;
}
code
{
  font-family: source-code-pro,Menlo,Monaco,Consolas, "Courier New", "monospace";
}
header{
  font-size: 1.5em;
  text-align: center;
}
.Nav
{
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  flex-direction: column;
  padding: 20px;
}
#search
{
  width: 80%;
  font-size: 24px;
}
Nav ul
{
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  background-color: transparent;
  width: 100%;
}
Nav ul li
{
  font-size: 24px;
  list-style-type: none;
  background-color: tomato;
  height: 100%;
  width: 100%;
}
Nav ul li:hover
{
  background-color: white;
}
li:hover a
{
  color: tomato;
}
Nav ul li a{
  color: #fff;
  text-decoration: none;
}
.App
{
  text-align: center;
}

